 {\n    filter: hue-rotate(-100deg);\n  }\n}\n@keyframes opacityRotate {\n  0%,\n  100% {\n    opacity: 0.9;\n  }\n  50% {\n    opacity: 0.75;\n  }\n}\n@keyframes boxShadowHum {\n  0%,\n  100% {\n    box-shadow: 0 0 0 $grey;\n  }\n  50% {\n    box-shadow: 0 -40px 40px $grey;\n  }\n}\n@keyframes saturateRotate {\n  0%,\n  100% {\n    filter: saturate(1);\n  }\n  50% {\n    filter: saturate(1.2);\n  }\n}\n@keyframes float {\n  0%,\n  100% {\n    transform: translateY(-3px);\n  }\n  50% {\n    transform: translateY(3px);\n  }\n}\n@keyframes playTape {\n  100% {\n    transform: rotate(360deg);\n  }\n}\n","@import '../../scss';\n\n.Quote {\n  display: flex;\n  flex-direction: column;\n  margin: 3.2em auto;\n  max-width: 75%;\n  position: relative;\n  row-gap: 8px;\n  .Quote--QuotationMark {\n    position: absolute;\n    font-size: 140px;\n    font-size: 220px;\n    font-weight: bold;\n    line-height: 1;\n    opacity: 0.2;\n    @include noSelect();\n    &.Left {\n      right: 110%;\n      top: 33%;\n    }\n    &.Right {\n      left: 110%;\n      bottom: 33%;\n    }\n  }\n  > p {\n    font-style: italic;\n    color: rgba($black, 0.85);\n    letter-spacing: 0.3px;\n  }\n  .Quote--Author {\n    align-self: flex-end;\n    font-style: italic;\n  }\n}\n","@import './colors.scss';\n\n@mixin boxShadow() {\n  box-shadow: 0 1px 2px $grey;\n  transition: box-shadow 75ms ease;\n  &:hover {\n    box-shadow: 0 2px 3px $grey;\n  }\n}\n\n@mixin clickAnimation($opacity: 0.5, $time: 150ms) {\n  opacity: 1;\n  transform: $time ease;\n  &:hover {\n    transform: translateY(-1px);\n    opacity: $opacity;\n  }\n  &:active,\n  &.active {\n    transform: translateY(2px);\n  }\n}\n\n@mixin antialiasing() {\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n@mixin grabbing() {\n  cursor: grabbing;\n  cursor: -moz-grabbing;\n  cursor: -webkit-grabbing;\n}\n@mixin noSelect() {\n  -webkit-touch-callout: none;\n  -webkit-user-select: none;\n  -khtml-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n\n@mixin appBackground() {\n  background-color: $pink;\n  animation: hueRotate infinite 40s;\n}\n"],"sourceRoot":""}]);
